import BaseService from './base-service.js'

function get (url, request) { return BaseService.get(url, request).then(response => { return response }) }
function post (url, request) { return BaseService.post(url, request).then(response => { return response }) }
function put (url, request) { return BaseService.put(url, request).then(response => { return response }) }
function del (url, request) { return BaseService.del(url, request).then(response => { return response }) }

function createService (url) {
  return {
    get: request => { return get(url, request) },
    details: (id, request) => { return get(url + '/' + id, request) },
    add: (request) => { return post(url, request) },
    edit: (id, request) => { return put(url + '/' + id, request) },
    del: (id, request) => { return del(url + '/' + id, request) }
  }
}
const Search = {
  get: request => { return get('global_search', request) } // 全局搜索
}
const Roles = createService('role')
const Users = createService('user')
// 基本信息
const EssentialInformation = createService('college_information')  // 学部简况 -> 基本信息 + 分支机构
const ScientificResearchPersonnel = createService('researcher')  // 学部人员 -> 科研人员
const TalentPlan = createService('talent_plan')  // 学部人员 -> 人才计划
const AcademicCommittee = createService('academic_committee')  // 学部人员 -> 学术委员会
const EthicsCommittee = createService('ethics_committee')  // 学部人员 -> 伦理委员会
const CollegeArea = createService('college_area')  // 学部设施 -> 实验场地
const CollegeDevice = createService('college_device')  // 学部设施 -> 研究设备
// 学术成果
const AcademicWorks = createService('academic_works')  // 学术著作
const PeriodicalPaperChinese = createService('journal_chinese_paper')  // 期刊论文 - 中文
const PeriodicalPaperEnglish = createService('journal_english_paper')  // 科研获奖 - 英文
const ResearchProjectsCountry = createService('research_project_portrait')  // 科研项目 - 国家级项目
const ResearchProjectsProvince = createService('research_project_province')  // 科研项目 - 省部级项目
const ResearchProjectsOther = createService('research_project_portrait_other')  // 科研项目 - 其他项目
const ResearchProjectsLandscape = createService('research_project_landscape')  // 科研项目 - 横向项目
const ResearchAwards = createService('research_awards')  // 科研获奖
const Patent = createService('patent')  // 发明专利
const MeetingPaper = createService('meeting_paper')  // 会议论文
const ResearchReport = createService('research_report')  // 研究报告
const HostMeeting = createService('host_meeting')  // 主办会议/培训
const ParticipateMeeting = createService('participate_meeting')  // 参与会议/培训
const Proportion = createService('proportion')  // 评分比例
const Score = createService('score')  // 评分
// 人才培养
const MasterTraining = createService('master_training')  // 硕士培养
const DoctorTraining = createService('doctor_training')  // 博士培养
const TeacherAward = createService('teacher_award')  // 教学获奖
const GoodCourse = createService('good_course')  // 精品课程
const TeachingMaterialConstruction = createService('teaching_material')  // 教材建设
const StudentAwards = createService('student_awards')  // 学生获奖情况
const StudentWorks = createService('student_works')  // 学生发表著作情况
const StudentMeeting = createService('student_meeting')  // 学生境内/境外参加会议
const StudentStudy = createService('student_study')  // 学生境内/境外交流学习情况
const StudentMeetingPaper = createService('student_meeting_paper')  // 学生发表会议论文
const PracticeBase = createService('practice_base')  // 实践基地
const PartTimeTutor = createService('part_time_tutor')  // 兼职导师
const ParticipateProfessionalPractice = createService('participate_practice')  // 参加专业实践活动
const GraduateEnrollment = createService('graduate_enrollment')  // 招生与毕业情况

const RulesRegulations = createService('rules_regulations')  // 规章制度
const Notice = createService('content_management')  // 公告
const Customize = createService('customize')  // 自定义字段
const PageSet = createService('page_set')  // 页面设置
PageSet.setting = (id, request) => { return put('visible/' + id, request) }

const Statistics = {
  get: request => { return get('count', request) } // 统计
}
const StatisticsChart = {
  get: request => { return get('count_pic', request) } // 统计图表(年度简报)
}
const Approval = {
  get: request => { return get('todo', request) }, // 待审批列表
  agree: (id, request) => { return put('check/' + id, request) }, // 同意
  refuse: (id, request) => { return del('check/' + id, request) } // 拒绝
}
const Backups = createService('back_up')  // 备份

function UpdateUsersRoles (request) { return post('update_Users_Roles', request) } // 批量修改用户分组
function FileDownload (request) { return get('compressed_download', request) }

