import Vue from 'vue'
import { API_URL, BASE_URL } from '@/assets/config'
import Cache from '@/assets/cache'
Vue.http.options.headers = {};
Vue.http.options.headers.Authorization = Cache.getToken()

function get (url, request) {
  return Vue.http.get(API_URL + url, { params: request })
    .then(response => {
      if (response.data.code == 500) {
        toastr.error(response.data.msg)
      }
      return response['data']
    })
    .catch(error => {
      responseError(error)
      return { code: 501 }
    })
}

function post (url, request) {
  return Vue.http.post(API_URL + url, request)
    .then(response => {
      return response['data']
    })
    .catch(error => {
      responseError(error)
      return { code: 501 }
    })
}

function put (url, request) {
  return Vue.http.put(API_URL + url, request)
    .then(response => {
      return response['data']
    })
    .catch(error => {
      responseError(error)
      return { code: 501 }
    })
}

function patch (url, request) {
  return Vue.http.patch(API_URL + url, request)
    .then(response => {
      if (response.data.code == 500) {
        toastr.error(response.data.msg)
      }
      return response['data']
    })
    .catch(error => {
      responseError(error)
      return { code: 501 }
    })
}

function del (url, request) {
  if (request) {
    url = url + getUrlQuery(request)
  }
  return Vue.http.delete(API_URL + url)
    .then(response => {
      if (response.data.code == 500) {
        toastr.error(response.data.msg)
      }
      if (response.data.code == 200) {
        swal('删除成功！', '您已经永久删除了这条数据。', 'success')
      }
      return response['data']
    })
    .catch(error => {
      responseError(error)
      return { code: 501 }
    })
}

function responseError (error) {
  if (error.body.error == "token_invalid") {
    toastr.error('请重新登录')
    setTimeout(() => {
      parent.location.href = BASE_URL + 'login'
    }, 1000)
  } else {
    const msg = error.body.message
    toastr.error(msg)
  }
}

function tokenExpire () {

}

function getUrlQuery (request) {
  let result = '?'
  let isFirst = true
  for (const key in request) {
    if (isFirst) {
      isFirst = false
      result += key + '=' + request[key]
    } else {
      result += '&' + key + '=' + request[key]
    }
  }
  return result
}

export default { get, post, put, del, patch }



